/* Font */
@import url("https://fonts.googleapis.com/css?family=Quicksand:400,700");
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap");

/* Design */
*,
*::before,
*::after {
  box-sizing: border-box;
}

.padtop {
  padding-top: 50px;
}

.cont {
  position: relative;
  min-height: 70vh;
  /* background-color: rgb(240, 240, 240); */
  /* opacity: 0.4; */
}

.cont:after {
  content: "";
  background-image: url("../images/utils/dronepic.jpg") center center no-repeat;
  background-size: cover;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: -1;
  opacity: 0.4; /* Here is your opacity */
}

.main {
  max-width: 1400px;
  margin: 1rem auto;
  border-radius: 1.5rem;
  min-width: 18rem;
}

.heading {
  /* font-family: 'Roboto', sans-serif; */
  text-align: center;
  color: #454343;
  font-size: 30px;
  font-weight: 700;
  position: relative;
  margin-bottom: 70px;
  text-transform: uppercase;
  z-index: 999;
  padding-top: 2rem;
}
.white-heading {
  color: rgb(56, 55, 55);
}

.heading:after {
  content: " ";
  position: absolute;
  top: 100%;
  left: 50%;
  height: 40px;
  width: 180px;
  border-radius: 4px;
  transform: translateX(-50%);
  background-repeat: no-repeat;
  background-position: center;
}

.white-heading:after {
  /* background: url(https://i.ibb.co/d7tSD1R/heading-line-white.png); */
  background-repeat: no-repeat;
  background-position: center;
}

.header span {
  font-size: 18px;
  display: block;
  font-weight: 500;
}

.white-heading span {
  color: #ffffff;
}

img {
  max-height: 100%;
  max-width: 100%;
  vertical-align: middle;
  object-fit: cover;
  transition: transform 400ms ease-out;
  max-width: 100%;
  height: auto;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
}

.forphone1{
  height: 15rem ;
}
.forphone2{
  height: 4rem;
}
.forphone3{
  height: 6rem;
}

.scrollerproj
{
	overflow-y: auto;
  scrollbar-width: thin;
}

.force-overflow
{
	min-height: 100px;
}

#style-401::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;
	background-color: transparent;
}

#style-401::-webkit-scrollbar
{
	width: 8px;
	background-color: transparent;
}

#style-401::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: rgb(204, 67, 67);
}

.evavishkar{
  height: 18rem;
}

.evavishkar:hover {
  transform:scale(1.2);
  -ms-transform:scale(1.2); /* IE 9 */
  -moz-transform:scale(1.2); /* Firefox */
  -webkit-transform:scale(1.2); /* Safari and Chrome */
  -o-transform:scale(1.2); /* Opera */
}

.evfeatured{
  height: 30vh;
}

.evfeatured:hover {
  transform:scale(1.2);
  -ms-transform:scale(1.2); /* IE 9 */
  -moz-transform:scale(1.2); /* Firefox */
  -webkit-transform:scale(1.2); /* Safari and Chrome */
  -o-transform:scale(1.2); /* Opera */
}

.card_image {
  display: inline-block; /* change the default display type to inline-block */
  overflow: hidden;      /* hide the overflow */
}

.cards_item:hover {
  transform: scale(1.08);
  transition: transform 400ms ease-out;
}

.btns {
  color: rgb(240, 240, 240);
  padding: 0.8rem;
  font-size: 14px;
  text-transform: uppercase;

  background-color: rgb(29, 29, 29);
  font-weight: 400;
  display: block;
  width: 100%;
  cursor: pointer;
  border: 1px solid rgb(29, 29, 29);
}

.btns:hover {
  background-color: rgb(204, 67, 67);
 font-weight: 700;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
}

.cards_item {
  display: flex;
  padding: 1rem;
}

@media (min-width: 40rem) {
  .cards_item {
    width: 70%;
  }
  .card_content {
    height: auto;
  }
}
@media (max-width: 40rem) {
  .card_content{
    height: auto;
  }
  .phoneheight {
    height: auto !important;
  }
  .card_title{
    font-size: 1rem !important;
  }
  .forphone2{
    height: 4rem;
  }
  .card_text{
    font-size: 0.7rem !important;
  }
  .forphone3{
    height: 4rem;
  }
}

@media (min-width: 56rem) {
  .cards_item {
    width: 33.333%;
  }
  .avishkar_cont {
    height: 20vh !important;
  } 
}

@media only screen and (max-width:900px)
{
  .pagesp .image{
    display: none;
  }
}

.cardproj {
  background-color: white;
  box-shadow: 0 20px 40px -14px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 100%;
}

.card_content {
  padding: 1rem;
  background: transparent;
  border-radius: 0px 0px 0.5rem 0.5rem;
}

.card_title {
  color: rgb(204, 67, 67);
  font-size: 1.2rem;
  font-weight: 600;
  text-transform: capitalize;
  margin: 0;
  text-align: center;
  font-family: sans-serif;
  margin-bottom: 10px;
}

.card_text {
  color:rgb(29,29, 29);
  text-align: center;
  font-size: .9rem;
  line-height: 1.5;
  margin-bottom: 1.25rem;
  font-weight: 500;
  padding: 0 1.5rem;
  font-family: revert;
}

.card_text2 {
  color: #1d1d1d;
  text-align: center;
  font-size: .9rem;
  line-height: 1.5;
  margin-bottom: 1.25rem;
  font-weight: 500;
  padding: 0 1.5rem;
  font-family: revert;
}

.made_by {
  font-weight: 400;
  font-size: 13px;
  margin-top: 35px;
  text-align: center;
}